import { SyncLoader } from "react-spinners";

interface ButtonProps {
  children?: React.ReactNode;
  bgColor?: string;
  textColor?: string;
  onClick?: () => void;
  active?: boolean;
  iconSrc?: string;
  isLoading?: boolean;
  textInvert?: boolean;
}

export default function Button({
  children,
  bgColor = "bg-slate-200",
  textColor = "text-black",
  onClick,
  active = false,
  iconSrc,
  isLoading = false,
  textInvert = false,
}: ButtonProps) {
  const resolvedBgColor = active ? bgColor : "bg-slate-200";
  const paddingClass = children ? "px-10 py-0" : "";

  return (
    <button
      onClick={onClick}
      className={`
        h-[48px]
        min-w-[120px]
        rounded-full font-semibold text-sm 
        ${resolvedBgColor} ${textColor} border-2 border-slate-900
        flex items-center justify-center gap-1 shadow-[2px_2px_0px_0px_#0F172A]
        ${paddingClass}
        whitespace-nowrap
      `}
    >
      {isLoading && (
        <div className="absolute">
          <SyncLoader color="#ffffff" size={6} margin={2} />
        </div>
      )}

      <div
        className={`
      h-full flex items-center justify-center gap-1
      ${isLoading ? "invisible" : ""}
    `}
      >
        {iconSrc && (
          <img
            src={iconSrc}
            alt="아이콘"
            className={`w-4 h-4 ${
              textInvert && !active ? "filter invert" : ""
            }`}
          />
        )}
        <span className={textInvert && active ? "filter invert" : ""}>
          {children}
        </span>
      </div>
    </button>
  );
}
